import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home
  },
  {
    path: '/opinfo',
    component: () => import('../views/opInfo.vue')
  },
  {
    path: '/attached',
    component: () => import('../views/attached.vue')
  },
  {
    path: '/unit',
    component: () => import('../views/Unit.vue')
  },
  {
    path: '/conver-unit',
    component: () => import('../views/ConverUnit.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
